import axios from 'axios'
import settings from '../settings'

axios.defaults.baseURL = settings.api.url

axios.interceptors.request.use((config) => {
  window.vm.$store.commit('openProgressLinear')
  const token = window.localStorage.getItem('token')
  if (token) {
    config.headers.Authorization = token
  }
  return new Promise((resolve) => {
    // Recaptcha
    window.grecaptcha.ready(() => {
      window.grecaptcha.execute(settings.recaptcha.siteKey).then((token) => {
        config.headers['captcha-token'] = token
        resolve()
      })
    })
  }).then(() => {
    return config
  })
}, (error) => {
  // Do something with request error
  return Promise.reject(error)
})

axios.interceptors.response.use((response) => {
  // 
  window.vm.$store.commit('closeProgressLinear')

  // status:0 
  // 
  if (response.data.status === 0) {
    window.vm.$store.commit('openSnackbar', {
      type: 'error',
      text: response.data.message
    })
  }

  // status:2 
  // 
  if (response.data.status === 2) {
    localStorage.removeItem('token')
    localStorage.removeItem('exp')
    window.vm.$router.push('/passport').then()
    window.vm.$store.commit('openSnackbar', {
      type: 'error',
      text: ''
    })
  }

  // status:3 
  // token
  if (response.data.status === 3) {
    localStorage.removeItem('token')
    localStorage.removeItem('exp')
    window.vm.$router.push('/passport').then()
    window.vm.$store.commit('openSnackbar', {
      type: 'error',
      text: ''
    })
  }

  return response
}, (error) => {
  return Promise.reject(error)
})

export default axios
